import { useState, useEffect } from "react";
import TopCustomers from "@/components/TopCustomers";
import LeastCustomers from "@/components/LeastCustomers";
import { Customer } from "@/components/Customer";

export default function CustomersPage() {
    const [customers, setCustomers] = useState<Customer[]>([]);

    useEffect(() => {
        fetch("/api/customers")
            .then((response) => response.json())
            .then((data) => setCustomers(data));
    }, []);

    return (
        <div>
            <TopCustomers customers={customers} title="Top Customers" iconColor="text-green-500" />
            <LeastCustomers customers={customers} title="Least Active Customers" iconColor="text-red-500" />
        </div>
    );
}